* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

a {
  background-color: transparent;
}

abbr {
  border-bottom: 0;
  cursor: help;
  font-variant: normal;
  text-decoration: none;
}

img {
  vertical-align: middle;
}

:root {
  word-wrap: break-word;
  -webkit-text-size-adjust: 100%;
  background: #fafafa;
  color: #4d4f62;
  font: 400 100%/1.6667 "Poppins", Tahoma, Geneva, sans-serif;
  min-width: 12.5rem;
  overflow-wrap: anywhere;
  text-align: center;
  text-rendering: optimizeSpeed;
  width: 100%;
}

body {
  font-size: 93.75%;
  padding: 5rem 0;
}

.wrap {
  margin: 0 2rem;
}

.header {
  margin: 0 auto 4rem;
  max-width: 33.75rem;
}

.header-titles {
  margin-bottom: 1rem;
}

.header-title,
.header-subtitle {
  font-size: 2.25rem;
  letter-spacing: 0.00694em;
  line-height: 1.4167;
}

.header-title {
  font-weight: 200;
  font-weight: 275;
}

.header-subtitle {
  font-weight: 600;
}

.header-txt {
  letter-spacing: 0.00694em;
  opacity: 0.5;
}

.tools {
  columns: 3;
  column-gap: 1.875rem;
  margin: 0 auto;
  max-height: 37rem;
  max-width: 69.375rem;
}

.wrap-tool {
  border-radius: 0.5rem;
  box-shadow: 0 0.9375rem 1.875rem -0.6875rem rgba(131, 166, 210, 0.5);
  display: inline-block;
  margin-bottom: 1.875rem;
  max-width: 21.875rem;
  overflow: hidden;
  padding-top: 0.25rem;
  vertical-align: middle;
  width: 100%;
}

.wrap-tool:last-of-type,
.wrap-tool:first-of-type {
  margin: 8.75rem 0;
}

.wrap-svisor {
  background: #44d3d2;
}

.wrap-tbuilder {
  background: #ea5454;
}

.wrap-karma {
  background: #fcae4a;
}

.wrap-calc {
  background: #549ef2;
}

.tool {
  background: #fff;
  padding: 1.75rem 2rem 2rem;
  text-align: right;
}

.tool-title,
.tool-txt {
  text-align: left;
}

.tool-title {
  word-wrap: normal;
  font-size: 1.25rem;
  font-weight: 600;
  line-height: 1.5;
  margin-bottom: 0.375rem;
  overflow-wrap: normal;
  text-transform: capitalize;
}

.tool-txt {
  font-size: 0.8125rem;
  letter-spacing: 0.00694em;
  line-height: 1.7692;
  margin-bottom: 2.5rem;
  opacity: 0.5;
}

.attribution {
  font-size: 74%;
  min-width: 100%;
  padding: 0 3% 1rem;
  position: absolute;
}

.attribution a {
  color: #549ef2;
  white-space: nowrap;
}

.attribution abbr {
  cursor: inherit;
}

@media (max-width: 62.5rem) {
  .tools {
    column-count: 2;
    max-width: 45.625rem;
  }
  .wrap-tool:last-of-type,
  .wrap-tool:first-of-type {
    margin: 0 0 1.875rem;
  }
}
@media (max-width: 42.5rem) {
  body {
    padding: 5.3125rem 0 4.875rem;
  }
  .header {
    margin-bottom: 4.75rem;
  }
  .header-title,
  .header-subtitle {
    font-size: 1.5rem;
    line-height: 1.4583;
  }
  .tools {
    column-count: 1;
    max-height: 100%;
  }
  .wrap-tool,
  .wrap-tool:last-of-type,
  .wrap-tool:first-of-type {
    margin: 0 0 1.5625rem;
  }
  .tool {
    padding: 1.5rem 1.75rem 1.75rem;
  }
  .tool-title {
    margin-bottom: 0.0625rem;
  }
  .tool-txt {
    margin-bottom: 2.0625rem;
  }
  .tool-icon {
    width: 3.5625rem;
    height: auto;
  }
}
@media (max-width: 20rem) {
  body {
    padding: 22.6667% 0;
    position: relative;
  }
  .wrap {
    margin: 0 8.5333%;
  }
}